  :where(p, li, dt, dd, blockquote, figcaption, small, pre, code, cite, small) {
  margin: 0.85em 0;
}
:where(.n-typeset) :where(dd) {
  margin-inline-start: 0.85em;
}
.n-typescale-xxxl,
:where(.n-typeset h1) {
  font-size: var(--n-font-size-xxxl);
}
.n-typescale-xxl,
:where(.n-typeset h2) {
  font-size: var(--n-font-size-xxl);
}
.n-typescale-xl,
:where(.n-typeset h3) {
  font-size: var(--n-font-size-xl);
}
.n-typescale-l,
:where(.n-typeset) :where(h4, blockquote) {
  font-size: var(--n-font-size-l);
}
.n-typescale-m,
:where(.n-typeset) :where(p, h5) {
  font-size: var(--n-font-size-m);
}
:where(.n-typeset h5) {
  font-weight: var(--n-font-weight-heading);
}
.n-typescale-s,
:where(.n-typeset) :where(h6, figcaption) {
  font-size: var(--n-font-size-s);
  line-height: var(--n-line-height);
}
.n-typescale-xs,
:where(.n-typeset small) {
  font-size: var(--n-font-size-xs);
}
:where(.n-typeset) > :first-child {
  margin-block-start: 0;
}
:where(.n-typeset) > :last-child {
  margin-block-end: 0;
}
:where(.n-typeset) :where(ul, ol, dl, blockquote) {
  margin: 0.5em 0;
  padding: 0 0 0 1.5em;
}
:where(.n-typeset a) {
  color: var(--n-color-text-link);
  text-decoration: underline;
}
:where(.n-typeset a:hover) {
  text-decoration: none;
}
:where(.n-typeset code) {
  overflow-wrap: break-word;
}
:where(.n-typeset pre code) {
  white-space: pre-wrap;
  word-break: break-all;
}
:where(.n-typeset) :where(strong, b) {
  font-weight: var(--n-font-weight-strong);
}
:where(.n-typeset mark) {
  background: var(--n-color-status-warning-weak);
}
.n-caption {
  color: var(--n-color-text-weaker);
  font-weight: var(--n-font-weight);
}
.n-dl {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(9ch, max-content));
  column-gap: var(--n-space-m);
}
.n-dl dt {
  color: var(--n-color-text-weaker);
  font-weight: var(--n-font-weight-active);
  font-size: var(--n-font-size-s);
  padding-block-start: calc(var(--n-font-size-m) - var(--n-font-size-s));
  grid-column-start: 1;
}
.n-dl :where(dt, dd) {
  margin: 0;
}
.n-dl dd {
  color: var(--n-color-text);
  font-weight: var(--n-font-weight);
  font-size: var(--n-font-size-m);
  margin-block-end: var(--n-space-m);
}
.n-dl dd:last-of-type {
  margin-block-end: 0;
}
.n-color-accent,
.n-color-accent-bg {
  background-color: var(--n-color-accent);
}
.n-color-accent-text {
  color: var(--n-color-accent);
}
.n-color-accent-fill {
  fill: var(--n-color-accent);
}
.n-color-accent-stroke {
  stroke: var(--n-color-accent);
}
.n-stack,
.n-stack-horizontal,
.n-stack-horizontal-e,
.n-stack-horizontal-s {
  display: flex;
  justify-content: flex-start;
  flex-flow: column wrap;
}
:where(
    .n-stack,
    .n-stack-horizontal,
    .n-stack-horizontal-e,
    .n-stack-horizontal-s
  ) {
  gap: var(--n-space-m);
}
.n-stack-horizontal,
.n-stack-horizontal-e,
.n-stack-horizontal-s {
  align-items: center;
  flex-direction: row;
}
.n-stack-horizontal-s {
  align-items: start;
}
.n-stack-horizontal-e {
  align-items: end;
}
.n-grid,
.n-grid-12,
.n-grid-2,
.n-grid-3,
.n-grid-4,
.n-grid-6,
.n-grid-8 {
  display: grid;
  align-items: start;
  grid-template-columns: repeat(var(--n-grid-columns, 12), 1fr);
}
:where(
    .n-grid,
    .n-grid-12,
    .n-grid-2,
    .n-grid-3,
    .n-grid-4,
    .n-grid-6,
    .n-grid-8
  ) {
  gap: var(--n-space-m);
}
.n-grid-8 {
  --n-grid-columns: 8;
}
.n-grid-6 {
  --n-grid-columns: 6;
}
.n-grid-4 {
  --n-grid-columns: 4;
}
.n-grid-3 {
  --n-grid-columns: 3;
}
.n-grid-2 {
  --n-grid-columns: 2;
}
.n-grid-center-i {
  justify-self: center;
}
.n-grid-center-b {
  align-self: center;
}
.n-grid-center {
  place-self: center;
}
.n-container,
.n-container-l {
  max-inline-size: 1200px;
}
.n-container-xl {
  max-inline-size: 2400px;
}
.n-container-m {
  max-inline-size: 1000px;
}
.n-container-s {
  max-inline-size: 800px;
}
.n-container-xs {
  max-inline-size: 600px;
}
.n-container-xxs {
  max-inline-size: 400px;
}
.n-truncate {
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.n-truncate-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  box-orient: vertical;
  overflow: hidden;
}
.n-truncate-3 {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  line-clamp: 3;
  -webkit-box-orient: vertical;
  box-orient: vertical;
  overflow: hidden;
}
.n-align-start {
  text-align: start;
}
.n-align-center {
  text-align: center;
}
.n-align-end {
  text-align: end;
}
.n-divider,
.n-hr {
  background: var(--n-color-border);
  display: block;
  margin: 0;
  border: 0;
  block-size: 1px;
  inline-size: 100%;
}
.n-hint,
.n-label {
  display: flex;
  color: var(--n-color-text);
  font-family: var(--n-font-family);
  font-size: var(--n-font-size-m);
  font-weight: var(--n-font-weight-heading);
  line-height: var(--n-line-height-heading);
  margin: 0;
}
.n-hint {
  color: var(--n-color-text-weaker);
  font-weight: var(--n-font-weight);
  font-size: var(--n-font-size-s);
  line-height: var(--n-line-height-caption);
}
.n-error {
  color: var(--n-color-text-error);
  font-weight: var(--n-font-weight);
  font-size: var(--n-font-size-s);
  line-height: var(--n-line-height-caption);
}
.n-input {
  display: block;
  background: var(--n-color-active);
  color: var(--n-color-text);
  padding-block-start: calc(var(--n-space-s) - 1px);
  padding-block-end: calc(var(--n-space-s) - 1px);
  padding-inline-start: calc(var(--n-space-s) * 1.6);
  padding-inline-end: calc(var(--n-space-s) * 1.6);
  border-radius: var(--n-border-radius-s);
  border: 1px solid var(--n-color-border-strong);
  font-family: var(--n-font-family);
  font-weight: var(--n-font-weight);
  font-size: var(--n-font-size-m);
  line-height: var(--n-line-height-form);
  transition: border var(--n-transition-slowly),
    box-shadow var(--n-transition-slowly), background var(--n-transition-slowly);
}
.n-input:hover {
  border-color: var(--n-color-border-hover);
}
.n-input:focus-within {
  border-color: var(--n-color-accent);
  background: var(--n-color-surface);
  outline: 0;
  box-shadow: 0 0 0 1px var(--n-color-accent);
}
.n-input::placeholder {
  color: var(--n-color-text-weakest);
}
.n-input:disabled {
  border-color: var(--n-color-active);
  color: var(--n-color-text-weakest);
}
.n-input-invalid,
.n-input[aria-invalid="true"] {
  border-color: var(--n-color-status-danger);
}
.n-input-invalid:focus-within,
.n-input[aria-invalid="true"]:focus {
  border-color: var(--n-color-status-danger);
  box-shadow: 0 0 0 1px var(--n-color-status-danger);
}
.n-clinic-icon,
.n-clinic-icon-s {
  color: var(--n-color-text-on-accent);
  background: var(--n-clinic-icon-color, var(--n-color-accent));
  border-radius: var(--n-border-radius);
  box-shadow: var(--n-box-shadow);
  inline-size: var(--n-clinic-icon-size, var(--n-size-icon-l));
  block-size: var(--n-clinic-icon-size, var(--n-size-icon-l));
  min-inline-size: var(--n-clinic-icon-size, var(--n-size-icon-l));
  min-block-size: var(--n-clinic-icon-size, var(--n-size-icon-l));
  line-height: var(--n-clinic-icon-size, var(--n-size-icon-l));
  font-size: var(--n-clinic-icon-font-size, var(--n-font-size-s));
  font-weight: var(--n-font-weight-active);
  display: inline-block;
  text-align: center;
}
.n-clinic-icon-s {
  --n-clinic-icon-size: calc(var(--n-size-icon-l) / 1.2);
}
.n-counter {
  display: block;
  inset-block-start: calc(var(--n-space-s) * -1);
  inset-inline-end: calc(calc(var(--n-space-s) + var(--n-space-xs)) * -1);
  pointer-events: none;
  min-inline-size: 17px;
  line-height: var(--n-line-height-tight);
  border-radius: var(--n-border-radius-pill);
  color: #fff;
  text-align: center;
  font-size: 10px;
  padding: calc(var(--n-space-xs) - 1.5px) var(--n-space-xs);
  z-index: var(--n-index-mask);
  font-feature-settings: var(--n-font-features-reduced);
  font-weight: var(--n-font-weight-heading);
  box-shadow: 0 0 0 1px var(--n-color-accent-secondary),
    0 1px 5px rgba(0, 0, 0, 0.4), inset 0 0 5px rgba(255, 255, 255, 0.2);
  background: var(--n-color-status-notification);
}
